import React, { useState, useEffect } from "react";
import Input from "../Common/Input";
import Button from "../Common/Button";
import "./index.less";
import CenteredBlock from "../Common/CenteredBlock";
import { useDispatch, useSelector } from "react-redux";
import { useNavigate } from "react-router";
import { User2FaOtpVerifyAction } from "../../requests/userRequests";

const TwoFactorVerify = () => {
  const dispatch = useDispatch();
  const navigate = useNavigate();
  const loggedIn = useSelector((store) => store.user.loggedIn);
  const messages = useSelector((store) => store.user.messages);
  const [otp, setOtp] = useState("");
  useEffect(() => {
    loggedIn && navigate("/client-user");
  }, [loggedIn]);
  const handleOnChange = (type, event) => {
    setOtp(event.target.value);
  };
  const handleOnClick = (event) => {
    event.preventDefault();
    dispatch(User2FaOtpVerifyAction(otp));
  };
  return (
    <CenteredBlock title={"Two Factor Verification"}>
      <form onSubmit={(event) => handleOnClick(event)}>
        <Input
          type="otp"
          name="otp"
          title={`Verification code`}
          className=""
          placeholder={`Please enter 6 digits`}
          value={otp}
          error={messages && messages["otp"]}
          onChange={(event) => handleOnChange("otp", event)}
          onBlur={(event) => handleOnChange("otp", event)}
        />
        <Button
          type="submit"
          className="signin-btn btn-green"
          onClick={(event) => handleOnClick(event)}
        >
          {"Submit"}
        </Button>
      </form>
    </CenteredBlock>
  );
};

export default TwoFactorVerify;
